import * as types from '../constants/actionTypesBook';

//  from
export function changeFrom(data)
{
    return {
        type: types.FROM_CHANGE,
        data
    };
}

//  from
export function changeTo(data)
{
    return {
        type: types.TO_CHANGE,
        data
    };
}

//  epic 
export const bookGet$ = (data) =>
{
    return {
        type: types.BOOK_GET$,
        data
    };
};

// 
export function bookUpdate(data)
{
    return {
        type: types.BOOK_UPDATE,
        data
    };
}

