
const ENDPOINT = 'https://api.etherscan.io/api';
const TOPIC_FILLORDER = '0x7df4b7671e89cf44d438b410348aefa7f1fec4753f50d093449fa17b0660051d';

const apiKey = 'SH6JBSKW5GBSI8NJJD6JY837NIIRSFNZX3';

// TODO:
const genesisBlock = 544607;

function request(data, Url) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        const url = Url || serverUrl;
        const method = 'POST';
        xhr.open(method, url);
        xhr.onreadystatechange = () => {
            if (xhr.readyState === XMLHttpRequest.DONE) {
                resolve(xhr.response);
            }
        };
        xhr.onerror = () => {
            reject({
                status: this.status,
                statusText: xhr.statusText
            });
        };

        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.send(JSON.stringify(data));
    });
}

class Etherscan {
    /**
     * Get contract logs
     * @param  {String} contractAddress    The XPA Exchange address
     * @returns {Promise}          
    */
    static getFillOrderLogs(contractAddress) {
        console.log('xpa', contractAddress);
        const url = `${ENDPOINT}?module=logs&action=getLogs&fromBlock=${genesisBlock}&toBlock=lastest&address=${contractAddress}&topic0=${TOPIC_FILLORDER}&apiKey=${apiKey}`
        
        return new Promise((resolve, reject) => {
            request({}, url).then((res) => {
                const { result } = JSON.parse(res);
                const PARAM_LEN = 64;
                const ADDRESS_LEN = 40;
                const order = result.reverse().map((tx) => {
                    const { data } = tx;
                    const _data = data.replace('0x', '');

                    const fromToken = '0x' + _data.substr(0, PARAM_LEN).substr(PARAM_LEN - ADDRESS_LEN, ADDRESS_LEN);
                    const toToken = '0x' + _data.substr(PARAM_LEN * 1, PARAM_LEN).substr(PARAM_LEN - ADDRESS_LEN, ADDRESS_LEN);
                    const price = _data.substr(PARAM_LEN * 2, PARAM_LEN);
                    const user = _data.substr(PARAM_LEN  * 3, PARAM_LEN);
                    const amount = _data.substr(PARAM_LEN * 4, PARAM_LEN);

                    return {
                        no: tx.transactionHash,
                        fromAmount: amount,
                        fromToken,
                        price,
                        toToken,
                        type: 'trade', // or 'take' ?
                        user,
                        timestamp: parseInt(tx.timeStamp) * 1000
                    }
                })
                resolve(order);
            }).catch(e => reject(e))
        })
    }
}

export default Etherscan